// @flow
import {
  Flex,
  Text,
  Spacer,
  Image,
  Stack,
  Center,
  Divider,
  Heading,
  useToast,
  Box,
  Spinner,
} from "@chakra-ui/react";
import { lazy, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { nanoid } from "nanoid";
import { useDispatch, useSelector } from "react-redux";
import { calculateTotal, removeFromCart } from "../../redux/CartSlice";
import { RootState } from "../../redux/store";
import { Dispatch } from "redux";
import MeProtocolToast from "../../hooks/MeProtocolToast";
import AddBagNavbar from "../../components/AddbagNavbar";
import Container from "../../components/Container";
import Lottie from "lottie-react";
import emptyCartAnimation from "../../assets/json/108106-empty-cart.json";
import { GLOBAL_PREFIX } from "../../constants";
import NewDashboardNavBar from "@me-protocol/components/NewDashboardNavBar";
import { AuthReducerProps, UserReducerProps } from "@me-protocol/interfaces";
import { BASE_URL } from "@me-protocol/config";
import { getUserInfo } from "@me-protocol/redux/UserSlice";
import axios from "axios";
import { useMeProtocol } from "me-protocol";

const style = {
  height: 300,
};

export const Addbag = () => {
  let navigate = useNavigate();
  const dispatch = useDispatch<Dispatch>();
  const { user_data }: UserReducerProps = useSelector((state: RootState) => state.userReducer);
  const { token, session_id }: AuthReducerProps = useSelector(
    (state: RootState) => state.authReducer
  );

  const toast = useToast();
  const { loading, setUpWallet } = useMeProtocol();

  const { root, leftSection, rightSection } = useAddbagStyles();

  const { cartItems }: any = useSelector((state: RootState) => state.cartReducer);

  useEffect(() => {
    // console.log(cartItems, "OOPPPP");
    // dispatch(calculateTotal());
  }, [cartItems]);

  const CREATE_WALLET = async (product: any) => {
    try {
      const wallet: any = await setUpWallet({
        persist: false,
      });

      if (wallet.publicAddress) {
        await axios
          .put(
            `${BASE_URL}/customer/setup-wallet-address`,
            { walletAddress: wallet.publicAddress },
            { headers: { Authorization: `Bearer ${token}` } }
          )
          .then(({ data }: any) => {
            if (token) dispatch(getUserInfo({ token }));
            MeProtocolToast(toast, "Wallet created successfully", 2000);
            navigate(`${GLOBAL_PREFIX}/subsequentRedeeming`, {
              state: { ...product },
            });
          })
          .catch((e: any | unknown) => {
            console.log(e, "UPDATeYUSERZRR");
          });
      }
    } catch (error) {
      console.log(error, "CREATE_WALLETERR");
    }
  };

  return (
    <Flex flexDir={"column"}>
      {/* ----------------------------------------------------------------------NAV BAR------------------------------------------------------------ */}
      <NewDashboardNavBar />
      {/* <AddBagNavbar
        borderBottomWidth={"1px"}
        zIndex={-1000}
        // customPadding={"5%"}
      /> */}
      {/* ----------------------------------------------------------------------TOP CARDS------------------------------------------------------------ */}

      {cartItems?.length > 0 ? (
        <Container>
          <Stack {...root}>
            <Flex>
              {/* -----------------------------------LEFT SECTION [IMAGE SECTION]---------------------------------------------- */}
              <Flex {...leftSection} flexDir={"column"}>
                <Flex flexDir="column">
                  <Image
                    src={cartItems[0]?.offerImages[0].url || cartItems[1]?.images}
                    objectFit="contain"
                    w={"100%"}
                    h={"100%"}
                    bg="#00000020"
                  />
                </Flex>
              </Flex>

              <Spacer m={"1%"} />
              {/* -----------------------------------RIGHT SECTION---------------------------------------------- */}
              <Flex {...rightSection} flexDir="column">
                {cartItems?.map((e: any) => (
                  <Flex flexDir="column" key={nanoid()}>
                    <Flex flexDir={"row"} justifyContent={"space-between"}>
                      <Flex>
                        <Image
                          src={e?.offerImages[0].url}
                          w={"100px"}
                          h={"100px"}
                          objectFit="contain"
                          bg={"#00000020"}
                        />
                      </Flex>

                      <Flex flexDir={"column"} align="flex-start" pl="5px">
                        <Heading fontSize={"13px"} mb={"10px"}>
                          {e?.name}
                        </Heading>
                        <Spacer />
                        <Text fontSize={"14px"} color="#999999">
                          Black
                        </Text>
                        <Text fontSize={"14px"} color="#999999">
                          Size: {" S "}
                        </Text>
                        <Text fontSize={"14px"} color="#999999">
                          Quantity: {e?.quantity}
                        </Text>
                      </Flex>
                      <Spacer />

                      <Flex flexDir="column" align={"flex-end"}>
                        <Text fontSize={"13px"} mb={"6px"}>
                          {e?.tokens} {e?.reward?.rewardSymbol}
                        </Text>
                        <Text fontSize={"14px"} textAlign={["right"]} color="#999999" mb={"30px"}>
                          {e?.originalPrice} USD
                        </Text>
                        {/* ------------------------------- REMOVE IS HERE ----------------------------------- */}
                        <Text
                          fontSize={"14px"}
                          textAlign={["right"]}
                          color="#C90303"
                          as={"button"}
                          onClick={() => {
                            dispatch(removeFromCart(e?.id));
                            MeProtocolToast(toast, "Removed from cart", 2000);
                          }}
                        >
                          Remove
                        </Text>
                      </Flex>
                    </Flex>

                    {/* ------------------------------------ CHECK OUT BUTTON --------------------------------- */}
                    <Center
                      as="button"
                      border={"1px solid #000000"}
                      h="50px"
                      color="black"
                      mt={"12px"}
                      mb={"40px"}
                      _hover={{
                        background: "#000000",
                        color: "#FFFFFF",
                      }}
                      onClick={async () => {
                        // navigate(`${GLOBAL_PREFIX}/redeem`, { state: { ...e } });
                        if (user_data?.customer?.walletAddress) {
                          navigate(`${GLOBAL_PREFIX}/subsequentRedeeming`, {
                            state: { ...e },
                          });
                        } else {
                          await CREATE_WALLET(e);
                        }
                      }}
                    >
                      {loading ? <Spinner /> : "Checkout"}
                    </Center>

                    <Divider orientation="horizontal" mb={"30px"} />
                  </Flex>
                ))}
              </Flex>
            </Flex>
          </Stack>
        </Container>
      ) : (
        <Stack {...root} align="center">
          <Lottie animationData={emptyCartAnimation} style={style} />
          <Text pt="3%" fontSize="18px" fontWeight="600">
            You currently have no item in cart
          </Text>
        </Stack>
      )}
    </Flex>
  );
};

export default Addbag;

